            </div>
<router-view></router-view>
  </div>
</template>

<script>
export default {  
  data () {  
    return {  
         isActive:false
    }  
  },      
  methods: {  
    menucurrent: function () {
    if(this.isActive){
      this.$refs.nav.style.left = -110 +'vw';
      this.isActive = false
    } else {
      this.$refs.nav.style.left = 0;
      this.isActive = true
    }

 
    }  
  }

} 


function addLoadEvent(func){
    var oldonload=window.onload;
    if(typeof window.onload!="function"){
        window.onload=func;
    }else{
        window.onload=function(){
            oldonload();
            func();
        }
    }
}


function menucurrent(){
    var header = document.getElementsByClassName("header")[0]
    var odiv = header.getElementsByTagName("div")
    var odiv = header.childNodes
    for(var i=0;i<odiv.length;i++){
        odiv[i].index=i;
        odiv[i].onclick=function(){
           var odiv = header.childNodes
            for(var n=0;n<odiv.length;n++){
                odiv[n].className="button-base nav-item"
       }
              odiv[this.index].className="menu-current nav-item"
     }
   }
}


addLoadEvent(menucurrent)

</script>

